
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import {question} from '../config'
import Collapse from 'vue-collapse'
export default {
  name: 'question',
  head: {
    title: {
      inner: ''
    }
  },
  components: {
    Group,
    Cell,
    Collapse
  },
  data () {
    return {
      list: [],
      show: [],
      form: {
        limit: 10,
        pageIndex: 0
      }
    }
  },
  created () {
    this.$http({
      method: 'jsonp',
      url: question,
      jsonp: 'callback',
      jsonpCallback: 'json',
      params: this.form
    })
    .then(res => {
      this.list = res.body.data.qaList
    })
  },
  methods: {
  }
}



