export default {
  name: "Collapse",

  data(){
    return {
      active: false
    }
  },

  props: {
    selected: {
      type: Boolean,
      required: true,
      default: false
    }
  },

  created () {
    this._isCollapseItem = true
    this.active = this.selected
  },
  ready () {
    if (this.active) {
      this.$emit('collapse-open', this.index)
    }
  },
  methods: {
    toggle () {
      this.active = !this.active
      if (this.active) {
        this.$emit('collapse-open', this.index)
      }
    }
  }
}



